@import "../../_globalColor";

.repo-card-div {
  color: $githubRepoCardColor;
  background-color: $lightBackground2;
  box-shadow: $lightBoxShadowDark 0px 10px 30px -15px;
  padding: 1.5rem;
  border-radius: 16px;
  border: 1px solid $lightBorder1;
  cursor: pointer;
  -webkit-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
  height: 280px;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
  overflow: hidden;
  box-sizing: border-box;
}

.repo-card-div:hover {
  box-shadow: $lightBoxShadowDark 0px 20px 40px -10px;
  transform: translateY(-5px);
}

.repo-stats {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 12px;
  color: $githubRepoCardRepoCardStatsColor;
  margin-top: auto;
  padding-top: 1rem;
  border-top: 1px solid rgba(0, 0, 0, 0.05);
}

.repo-left-stat {
  flex-grow: 1;
  display: flex;
  align-items: center;
  gap: 1rem;
}

.language-color {
  width: 12px;
  height: 12px;
  background-color: $githubRepoCardLanguageColorBG;
  border-radius: 50%;
  flex-shrink: 0;
}

.repo-left-stat span {
  display: flex;
  align-items: center;
  gap: 0.25rem;
  font-weight: 500;
}

.repo-name-div {
  display: flex;
  align-items: center;
  margin-bottom: 1rem;
  min-height: 40px;
}

.repo-svg {
  margin-right: 0.75rem;
  min-width: 20px;
  height: 20px;
  fill: $githubRepoCardColor;
  flex-shrink: 0;
}

.repo-name {
  white-space: nowrap;
  text-overflow: ellipsis;
  color: $githubRepoCardRepoNameColor;
  font-size: 20px;
  font-weight: 700;
  letter-spacing: -0.5px;
  overflow: hidden;
  margin: 0;
  line-height: 1.2;
}

.repo-star-svg {
  width: 14px;
  height: 14px;
  fill: $githubRepoCardRepoCardStatsColor;
}

.repo-right-stat {
  font-weight: 600;
  color: $githubRepoCardRepoCardStatsColor;
}

.repo-description {
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  line-clamp: 3;
  -webkit-box-orient: vertical;
  color: $githubRepoCardRepoCardStatsColor;
  font-size: 14px;
  line-height: 1.5;
  margin: 0 0 1.5rem 0;
  flex-grow: 1;
  text-align: left;
}

.dark-card-mode {
  background-color: $darkBackground;
  color: $textColorDark;
  border-color: rgba(255, 255, 255, 0.1);
}

.dark-card-mode p {
  color: $textColorDark;
}

.dark-card-mode .repo-svg {
  fill: $textColorDark;
}

.dark-card-mode .repo-star-svg {
  fill: rgba(255, 255, 255, 0.7);
}

.dark-card-mode .repo-stats {
  border-top-color: rgba(255, 255, 255, 0.1);
}

.dark-card-mode:hover {
  background-color: $buttonColor !important;
  box-shadow: 0px 20px 40px rgba(0, 0, 0, 0.3);
  transform: translateY(-5px);
}

// Responsive design
@media (max-width: 768px) {
  .repo-card-div {
    height: 250px;
    padding: 1rem;
  }
  
  .repo-name {
    font-size: 18px;
  }
  
  .repo-description {
    font-size: 13px;
    -webkit-line-clamp: 2;
    line-clamp: 2;
  }
  
  .repo-stats {
    font-size: 11px;
  }
  
  .repo-left-stat {
    gap: 0.75rem;
  }
}

@media (max-width: 480px) {
  .repo-card-div {
    height: 220px;
    padding: 0.75rem;
  }
  
  .repo-name {
    font-size: 16px;
  }
  
  .repo-description {
    font-size: 12px;
  }
  
  .repo-name-div {
    margin-bottom: 0.75rem;
    min-height: 30px;
  }
  
  .repo-svg {
    width: 16px;
    height: 16px;
  }
}
